.profile .container {
  display: flex;
}
.profile__left-column {
  margin-right: 15px;
  flex-grow: 0;
  flex-shrink: 0;
}
.profile__right-column {
  flex-grow: 1;
}

@media (max-width: 1024px) {
  .profile__left-column {
    width: 232px;
  }
}
@media (max-width: 767px) {
  .profile .container {
    flex-direction: column;
  }
  .profile__left-column {
    width: 100%;
    margin-right: 0;
  }
}